/* Large Devices, Wide Screens */
@media only screen and (max-width : 1199px) {
  .header {
    &__catalog {
      &-btn {
        font-size: 0;
        width: 97px;
        span {
          margin-right: 0;
          vertical-align: 2px; } } }
    &__search {
      width: 294px; }
    &__links {
      &-li {
        width: 81px; }
      &-a {
        b {
          right: 17px; } } } }
  .main-catalog {
    &__block {
      &-img {
        &[src*='-2x'] {
          right: -90px; } } } }
  .faq {
    &__block {
      &-answer {
        padding-right: 20px; } } }
  .map {
    &__body {
      &-map {
        width: 66.59%; }
      &-list {
        width: 30.02%; }
      &-canvas {
        padding-bottom: 585px; } }
    &__carousel {
      height: 548px;
      &-block {
        height: 55px !important;
        &>div {
          height: 54px !important;
          white-space: normal;
          line-height: 20px;
          padding-top: 7px;
          padding-bottom: 7px;
          display: flex;
          align-items: center; } } } }
  .catalog {
    &__row {
      &-btn {
        width: 150px; } } } }

/* Medium Devices, Desktops */
@media only screen and (max-width : 991px) {
  .footer {
    &__navigation {
      &-title {
        font-size: rem(14); } } }
  .header {
    &__catalog {
      &-btn {
        font-size: 0;
        width: 50px; } }
    &__search {
      width: 220px; }
    &__links {
      &-li {
        width: 62px; }
      &-a {
        font-size: rem(9);
        b {
          right: 8px; } } }
    &__bottom {
      &-wrapper {
        &>div {
          &+div {
            margin-left: 22px; } } } } }
  .main-catalog {
    &__block {
      height: 180px;
      &-img {
        &[src*='-2x.png'] {
          height: 153px;
          right: -66px; }
        &[src*='-2.png'] {
          height: 140px; }
        &[src*='-3.png'] {
          height: 128px; }
        &[src*='-4.png'] {
          height: 134px; }
        &[src*='-5.png'] {
          height: 160px; } }
      &-title {
        font-size: rem(20);
        line-height: 25px;
        top: 20px;
        &::before {
          top: 3px; } }
      &-list {
        top: 100px;
        &>span {
          &:not(:last-child) {
            margin-bottom: 10px; } } }
      svg {
        width: 174px;
        height: 186px;
        right: -38px; } } }
  .main-tab {
    &__tab {
      font-size: rem(16);
      &>li {
        &:not(:last-child) {
          margin-right: 40px; }
        &>a {
          height: 40px;
          line-height: 40px; } } } }
  .product-table {
    &__table {
      .table {
        .table {
          &--product-name, &--product-info {
            display: table; }
          &--product-name {
            &>tbody {
              &>tr {
                background: none;
                &>td {
                  padding-left: 0;
                  padding-right: 0;
                  border: 0;
                  background: transparent;
                  padding-top: 15px;
                  padding-bottom: 5px;
                  height: auto; }
                &:hover {
                  &>td {
                    background: none; } } } } }
          &--product-info {
            max-width: 375px;
            width: 100%;
            table-layout: fixed;
            &>tbody {
              &>tr {
                background: none;
                &>td {
                  background: transparent;
                  border: 0;
                  padding-bottom: 5px;
                  height: auto;
                  // width: 33.3%
                  &.product-table__td-5 {
                    padding-left: 0; }
                  &.product-table__td-8 {
                    padding-right: 0;
                    width: 1px !important; } }
                &:hover {
                  &>td {
                    background: none; } } } } } } } } }
  .map {
    &__head {
      margin-top: -52px; }
    &__body {
      display: block;
      &-map {
        width: 100%;
        margin-bottom: 34px; }
      &-list {
        width: 100%;
        max-width: 470px;
        margin: 0 auto; } }
    &__carousel {
      height: 377px;
      &-block {
        height: 36px !important;
        &>div {
          height: 35px !important;
          white-space: nowrap; } } } }
  .search-result {
    &__head {
      &--cart {
        margin-top: -47px; } } }
  #filters {
    position: absolute;
    width: 250px;
    box-shadow: 2px 2px 10px rgba($color-2,0.1);
    visibility: hidden;
    opacity: 0;
    &.is-active {
      visibility: visible;
      opacity: 1; } }
  .filter {
    &__cost {
      position: relative;
      margin-bottom: 47px;
      .div-cell {
        &:last-child {
          padding: 0 !important; } }
      &-btn {
        position: absolute;
        width: 185px;
        left: 8px;
        top: 47px; } } }
  .catalog {
    &__mobile-btn {
      display: block; }
    &__head {
      padding-left: 26px; } } }

/* Small Devices, Tablets */
@media only screen and (max-width : 767px) {

  .animated {
    transition-property: none !important;
    transform: none !important;
    animation: none !important; }
  .footer {
    &__navigation {
      margin: 0 auto;
      max-width: 345px;
      &-title {
        font-size: rem(18);
        color: #eee; }
      &-block {
        float: none;
        width: 100%;
        padding: 0;
        &.is-active {
          .footer__navigation-list {
            display: block; } } }
      &-list {
        display: none;
        margin-bottom: 25px; } }
    &__card, &__copyright {
      text-align: center;
      float: none; } }
  .header {
    &__top {
      &-left, &-right {
        display: none; } }
    &__logo {
      position: absolute;
      margin-top: -31px;
      margin-left: 60px;
      img {
        width: 100px;
        height: 24px;
        background-image: url('../img/header-logo-mobile.png'); } }
    &__links {
      position: absolute;
      right: 50px;
      margin: -40px 0 0 0;
      &-li {
        width: auto;
        margin-right: 24px;
        &:last-child {
          margin-right: 0; }
        &.is-active {
          background: none;
          &>a {
            background: none !important; } } }
      &-a {
        height: 40px;
        padding-top: 9px;
        background: none !important;
        svg, img {
          width: 22px;
          height: 22px; }
        span, b {
          display: none; } }
      &-popup {
        top: 36px;
        &--user {
          left: inherit;
          right: 0;
          &::before {
            left: inherit;
            right: 41px; }
          &::after {
            left: inherit;
            right: 38px; } } } }
    &__bottom {
      &-wrapper {
        &>div {
          &:nth-child(2), &:last-child {
            margin-left: 0; }
          &:nth-child(3) {
            width: 100%;
            margin-left: 15px; } } } }
    &__search {
      width: 100%; }
    &__phone {
      display: block; }
    &-separator {
      height: 160px; } }
  .content {
    padding: 30px 0; }
  .main-catalog {
    margin: 0 auto;
    max-width: 357px;
    padding-bottom: 15px;
    &>div {
      padding: 0; }
    &__block {
      height: 115px;
      margin-bottom: 15px;
      &-list {
        display: none; }
      &-img {
        &[src*='-2x.png'] {
          height: 95px;
          right: -90px; }
        &[src*='-2.png'] {
          height: 90px; }
        &[src*='-3.png'] {
          height: 82px; }
        &[src*='-4.png'] {
          height: 94px; }
        &[src*='-5.png'] {
          height: 95px; } }
      svg {
        width: 111px;
        height: 119px;
        right: -24px; }
      &-title {
        font-size: rem(26);
        line-height: 30px;
        top: 16px;
        &::before {
          top: 6px; } } } }
  .main-tab {
    &__tab {
      font-size: rem(15);
      &>li {
        &:not(:last-child) {
          margin-right: 22px; } } }
    &__block {
      max-width: 350px;
      margin-left: auto;
      margin-right: auto; } }
  .breadcrumbs {
    margin-top: -15px; }
  .search-result {
    &__head {
      float: none;
      margin-top: 0;
      margin-bottom: 30px;
      &>div {
        &+div {
          padding-left: 25px; } }
      &--cart {
        float: right;
        margin: -47px 0 0 0; } } }
  .map {
    &__head {
      margin-top: 0;
      float: none;
      margin-bottom: 20px;
      margin-left: 0; } }
  .product-table {
    &__total {
      &-left {
        text-align: center;
        float: none;
        font-size: rem(13); }
      &-right {
        text-align: center;
        float: none;
        padding-top: 10px; } } }
  .catalog {
    &__row {
      &-wrapper {
        flex-wrap: wrap; }
      &-title {
        width: 100%;
        flex-shrink: 0; }
      &-img {
        width: 150px; }
      &-content {
        width: calc(100% - 150px);
        padding-left: 15px; }
      &-table {
        margin-bottom: 20px; }
      &-btn {
        margin-top: 0; } } }
  .pagination {
    &--catalog {
      float: none;
      width: 100%;
      text-align: center;
      margin-bottom: 20px;
      &>ul {
        display: inline-block; } } }
  .catalog {
    &__sort {
      float: none;
      justify-content: center; } } }

/* Extra Small Devices, Phones */
@media only screen and (max-width : 479px) {
  .footer {
    &__navigation {
      max-width: 190px;
      &-title {
        font-size: rem(16); } } }
  .header {
    &__logo {
      margin-left: 0; }
    &__links {
      right: 15px;
      &-popup {
        &-wrapper {
          width: 290px; }
        &--user {
          right: -58px; } } }
    &__catalog {
      &-img {
        display: none; }
      &-info {
        display: none; }
      &-popup {
        &-wrapper {
          width: 290px; } }
      &-body {
        width: 100%;
        box-shadow: none; } }
    &__cart {
      &-row {
        padding-left: 45px;
        &-img {
          display: none; } } } }
  .main-catalog {
    &__block {
      &-title {
        font-size: rem(20);
        line-height: 25px;
        &::before {
          top: 3px; } } } }
  .main-slider {
    margin-bottom: 55px;
    .slick-dots {
      bottom: -24px; } }
  .main-tab {
    &__tab {
      font-size: rem(10);
      &>li {
        &:not(:last-child) {
          margin-right: 10px; } } } }
  .content {
    &__wrapper {
      padding-left: 22px;
      padding-right: 22px; } }
  .hidden-xxs {
    display: none !important; }
  .visible-xxs {
    display: block !important; }
  .col-xxs-12 {
    width: 100% !important; }
  .faq {
    &__block {
      &-title {
        padding-left: 12px; }
      &-answer {
        padding-left: 12px; } } }
  .table__prices {
    &>tbody {
      &>tr {
        &>td {
          &:first-child {
            padding-left: 10px; } } } } }
  .product-table {
    &__table {
      margin-left: -5px;
      margin-right: -5px;
      .table {
        &>thead {
          &>tr {
            &>th {
              &:first-child {
                padding-left: 0; } } } }
        &>tbody {
          &>tr {
            &>td {
              padding-left: 5px !important;
              padding-right: 5px !important;
              &.product-table__td-1 {
                padding-left: 0 !important; } } } }
        .table--product-info {
          table-layout: auto; } } } }
  .table--cart {
    margin-left: -22px;
    width: calc(100% + 22px); }
  .map {
    &__popup {
      left: 0; }
    &__head {
      display: block;
      &>div {
        &+div {
          margin-left: 0;
          margin-top: 25px; } } }
    &__carousel {
      height: 548px;
      &-block {
        height: 55px !important;
        &>div {
          height: 54px !important;
          white-space: normal; } } } }
  .catalog {
    &__row {
      &-img {
        width: 100%;
        margin-bottom: 20px; }
      &-content {
        width: 100%;
        padding-left: 0; }
      &-btn {
        margin: 0 auto;
        display: block;
        float: none; } }
    &__mobile-btn {
      left: -26px;
      &--filter {
        left: inherit;
        right: -32px; } } } }

