        &:hover {
          color: $color-1;
          text-decoration: none; } } }
    &-date {
      background: $color-1;
      color: $color-white;
      font-size: rem(10);
      white-space: nowrap;
      height: 18px;
      line-height: 18px;
      padding: 0 10px;
      margin-bottom: 10px;
      display: inline-block; }
    p {
      line-height: 22px;
      &:last-child {
        margin-bottom: 0; } } } }

.faq {
  &__row {
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: 0; } }
  &__title {
    font-size: rem(15);
    line-height: 1.14;
    margin-bottom: 10px;
    font-family: $font-roboto-bold; }
  &__block {
    margin-bottom: 2px;
    border: 1px solid #f3f4f7;
    background: #f3f4f7;
    font-size: rem(13);
    transition: background 0.35s;
    &.is-active {
      background: $color-white;
