  &__info {
    font-size: rem(13);
    border: 2px solid $color-1;
    border-radius: 4px;
    background: $color-white;
    color: #444444;
    margin-bottom: 30px;
    padding: 20px 24px 24px 24px;
    p {
      line-height: 18px;
