.product-table {
  &__row {
    margin-bottom: 15px; }
  &__title {
    font-size: rem(15);
    font-family: $font-roboto-bold;
    text-transform: uppercase;
    color: $color-1;
    line-height: 24px;
    margin-bottom: 0px; }
  &__table {
    [role*="grid"], .product-table__datatables, .table--width100 {
      width: 100% !important;
      border-bottom: 0 !important; }
    .table {
      border-collapse: inherit;
      font-size: rem(12);
      &--product-name, &--product-info {
        display: none; }
      .table {
        margin-bottom: 0; }
      &>thead {
        &>tr {
          &>th {
            font-weight: 400;
            text-align: left;
            color: #8c8c8c;
            font-family: $font-roboto;
            text-transform: uppercase;
            padding: 13px 10px 13px 10px;
            border-bottom: 1px solid #dcdcdc;
            font-size: rem(11);
            width: auto !important;
            &.no-sort {
              background: none;
              cursor: default; } } } }
      &>tbody {
        &>tr {
          &>td {
            border-top: 1px solid $color-white;
            border-bottom: 1px solid #dcdcdc;
            background: $color-white;
            transition: all 0.35s;
            height: 36px;
            vertical-align: top;
            padding: 9px 10px;
            box-sizing: border-box; }
          &:hover {
            &>td {
